// Load speperate modules with destructure assign
import { Bar, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins
 
export default {
  extends: Bar,
  mixins: [reactiveProp],
  props: ['chartData', 'options'],
  mounted () {
    this.renderChart(this.chartData, this.options)
  }
}


